.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  justify-content: center;
  margin: 20px auto;
  display: grid;
}

.photo-img {
  max-width: 100%;
}

.search-form {
  justify-content: center;
  display: flex;
}

.load-more {
  cursor: pointer;
  color: #666;
  text-shadow: 0 1px #fff;
  background: #f9f9f9 linear-gradient(#f9f9f9 5%, #e9e9e9 100%);
  border: 1px solid #dcdcdc;
  border-radius: 6px;
  margin: 0 auto;
  padding: 16px 39px;
  font-family: Arial;
  font-size: 19px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  box-shadow: inset 0 1px #fff;
}

.load-more:hover {
  background: #e9e9e9 linear-gradient(#e9e9e9 5%, #f9f9f9 100%);
}

.load-more:active {
  position: relative;
  top: 1px;
}

.is-hidden {
  display: none;
}

